import Axios from "axios";
import React, { Component } from "react";
import Swal from "sweetalert2";
import { getUUID } from "../actions/fakturAction";
import FormMutasiCashDanBank from "./Component/FormMutasiCashDanBank";
import { connect } from "react-redux";
import PrintNota from "../Stocking/Component/PrintNota";
import { warning } from "../Alert";

const serverNonMaster = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
const maptostate = (state) => {
  return {
    noFaktur: state.provinsi.noFaktur,
    username: state.provinsi.username,
  };
};
class MutasiCashDanBank extends Component {
  constructor(props) {
    var date = new Date();
    var formatedDate = `${date.getFullYear()}-${
      date.getMonth() + 1
    }-${date.getDate()}`;
    super(props);
    this.state = {
      thisDay: formatedDate,
    };
  }

  componentDidMount() {
    this.props.dispatch(getUUID());
  }
  handleSubmit(data) {
    if (data.tanggal === undefined) {
      warning("Mohon isi tanggal");
      return false;
    } else if (data.jumlah === undefined) {
      warning("Mohon isi Jumlah");
      return false;
    } else if (data.asal === undefined) {
      warning("Mohon isi Asal");
      return false;
    } else if (data.tujuan === undefined) {
      warning("Mohon isi Tujuan");
      return false;
    }
    const isi = {
      alatBayarTunai: [
        {
          tunaiKurs: 0,
          tunaiRp: parseFloat(data.jumlah),
          tunaiNetto: 0,
          tunaiTipe: "-",
        },
      ],
      alatBayarTrf: [
        {
          transferTanggalJanji: "-",
          transferRp: parseFloat(data.jumlah),
          transferTipe: "-",
          accFrom: data.asal,
          accTo: data.tujuan,
          transferKurs: 0,
          transferNetto: 0,
        },
      ],
      noBon: this.props.noFaktur,
      tanggal: data.tanggal,
      noFaktur: this.props.noFaktur,
      tipeFaktur: "AMBIL UANG TUNAI",
      keterangan: data.keterangan,
      footerDocument: {
        inputBy: this.props.username, //INPUT BY DEFAULT
      },
    };
    console.log(isi);
    // INISIALISASI AUTOTABLE
    const tableRows = [];
    // for each ticket pass all its data into an array
    const ticketData = [
      "1",
      data.tanggal,
      parseFloat(data.jumlah).toLocaleString("id-ID"),
      data.asal,
      data.tujuan,
      parseFloat(data.jumlah).toLocaleString("id-ID"),
      data.keterangan,
    ];
    // push each tickcet's info into a row
    tableRows.push(ticketData);
    console.log(tableRows);
    var columTable = [
      "No",
      "Tanggal Janji",
      "Transfer nominal",
      "Nomor Rekening Asal",
      "Nomor Rekening Tujuan",
      "Transfer Netto",
      "Keperluan",
    ];
    // AKHIR INISIALISASI AUTOTABLE
    Axios.post(serverNonMaster + "fico/dc/fico", isi)
      .then((res) =>
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Transaksi Berhasil",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() => localStorage.removeItem("noFaktur"))
          .then(() => this.props.dispatch(getUUID()))
          .then(() =>
            PrintNota(
              "Tanggal",
              data.tanggal,
              "",
              "",
              "Nomor Bon",
              this.props.noFaktur,
              "",
              "",
              "ADMIN",
              data.tanggal,
              "",
              columTable,
              "MUTASI CASH DAN BANK",
              tableRows
            )
          )
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text:
            "Sepertinya terjadi kesalahan, silahkan check koneksi anda dan ulangi lagi transaksi",
          showConfirmButton: false,
          timer: 1500,
        })
      );
  }

  render() {
    return (
      <div className="container-fluid">
        <div className="col-lg-12 col-md-12">
          <div className="card">
            <div className="card-body">
              <div className="col-lg-12">
                <h1>Mutasi Cash Dan Bank</h1>
              </div>
              <div className="col-lg-12">
                <FormMutasiCashDanBank
                  onSubmit={(data) => this.handleSubmit(data)}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptostate, null)(MutasiCashDanBank);
