import Axios from "axios";
import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { createNumberMask } from "redux-form-input-masks";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",-",
  locale: "id-ID",
});
const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
const server = process.env.REACT_APP_BACKEND_URL;
class FormMutasiCashDanBank extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listKategori: [],
      listRekening: [],
    };
  }

  componentDidMount() {
    const date = new Date();
    this.props.change(
      "tanggal",
      `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`
    );
    Axios.get(server + "parameteruangs")
      .then((res) =>
        this.setState({
          listKategori: res.data,
        })
      )
      .catch((err) => console.log(err));
    Axios.get(server + "banks")
      .then((res) =>
        this.setState({
          listRekening: res.data,
        })
      )
      .catch((err) => console.log(err));
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-md-3 col-lg-3">
            <Field
              name="tanggal"
              label="Tanggal"
              type="date"
              value={this.state.thisDay}
              component={renderField}
              normalize={(value) => value && value.toUpperCase()}
            />
          </div>
          <div className="col-md-4 col-lg-4">
            <label htmlFor="">Asal</label>
            <Field
              name="asal"
              component="select"
              value="CASH"
              className="form-control"
            >
              <option value="DEFAULT" key="DEFAULT">
                silahkan pilih
              </option>
              <option value="CASH" key="CASH">
                CASH
              </option>
              <option value="CASH KECIL" key="CASH KECIL">
                CASH KECIL
              </option>
              {this.state.listRekening.map((list) => (
                <option value={list.noRekening} key={list.noRekening}>
                  {list.atasNama}
                </option>
              ))}
            </Field>
          </div>
          <div className="col-md-1 text-center">
            <label htmlFor="" className="text-white">
              .
            </label>
            <h3>
              <i className="fa fa-exchange-alt"></i>
            </h3>
          </div>
          <div className="col-md-4 col-lg-4">
            <label htmlFor="">Tujuan</label>
            <Field
              name="tujuan"
              component="select"
              value="CASH"
              className="form-control"
              normalize={(value) => value && value.toUpperCase()}
            >
              <option value="DEFAULT" key="DEFAULT">
                silahkan pilih
              </option>
              <option value="CASH" key="CASH">
                CASH
              </option>
              <option value="CASH KECIL" key="CASH KECIL">
                CASH KECIL
              </option>
              {this.state.listRekening.map((list) => (
                <option value={list.noRekening} key={list.noRekening}>
                  {list.atasNama}
                </option>
              ))}
            </Field>
          </div>
          <div className="col-lg-4 col-md-4">
            <label htmlFor="">Jumlah</label>
            <Field
              name="jumlah"
              component="input"
              type="tel"
              className="form-control"
              {...currencyMask}
            />
          </div>
          <div className="col-md-8 col-lg-8">
            <Field
              name="keterangan"
              label="Keterangan"
              type="text"
              component={renderField}
              normalize={(value) => value && value.toUpperCase()}
            />
          </div>
          <div className="col-lg-12 col-md-12">
            <div className="text-right">
              <button className="btn btn-primary" type="submit">
                Simpan
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormMutasiCashDanBank = reduxForm({
  form: "FormMutasiCashDanBank",
  enableReinitialize: true,
})(FormMutasiCashDanBank);
export default connect()(FormMutasiCashDanBank);
